<template>
  <div class="step">
    <div v-for="(item, index) in totalSteps" :key="index" class="item_list">
      <div class="child_list">
        <!-- 进度圈圈 -->
        <div class="left_cricle" :style="setStyle(index)" @click="onChange(index)">
          <!-- <span v-if="index >= currentStep">{{ index + 1 }}</span>
          <i v-else class="el-icon-check"></i> -->
          <span>{{ index + 1 }}</span>
        </div>
        <!-- 描述 -->
        <div class="middle_text">{{ stepsLabel[index] }}</div>
        <!-- 右边线条 -->
        <div
          v-if="index != totalSteps - 1"
          class="right_line"
          :style="setLine(index)"
        ></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'CtSteps',
  props: {
    stepsLabel: {
      // 步骤标题数组
      type: Array,
      default: () => {
        return []
      },
    },
    totalSteps: {
      // 步骤总数
      type: Number,
      default: 3,
    },
    currentStep: {
      // 当前选中的步骤
      type: Number,
      default: 0,
    },
  },
  methods: {
    onChange(index) {
      // 点击切换选择步骤
      // if (this.currentStep !== index) {
      //     this.localStep = index
      //     this.$emit('change', index)
      // }
    },
    setStyle(index) {
      return {
        background: index <= this.currentStep ? '#4880FF' : '#E1E1E1',
      }
    },
    setLine(index) {
      return {
        background: index < this.currentStep ? '#4880FF' : '#d2d2d2',
      }
    },
  },
}
</script>

