<!--
使用参考：
<batchUpload v-if="showUpload" :visible.sync = "showUpload" :title="batchTitle" :queryId = "queryId" :maxFileSize="10*1024*1024" >
  <template v-slot:tip>
    导入必须是xls文件；<br/>
    只导入EXCEL文件的第一个标签页；<br/>
    文件大小最大为10MB；<br/>
    号码最多10000个。
  </template>
</batchUpload>-->

<template>
  <el-dialog
    :close-on-click-modal="false"
    :title="title"
    :visible.sync="v"
    :append-to-body="true"
    width="840px"
    top="1vh"
  >
    <div v-loading="loading" :element-loading-text="$t('batchUpload.uploading')">
      <p class="download-tip">
        {{ $t('batchUpload.please')
        }}<a class="cor-blue" target="_self" @click="downloadTpl">{{
          $t('batchUpload.downloadTpl')
        }}</a
        >{{ $t('batchUpload.please2') }}
      </p>
      <div class="tip">
        <i class="icon-zhuyi" style="color: orange"></i>
        <p class="upload-tip">
          {{ $t('batchUpload.warmTip') }}<br /><slot name="tip"></slot>
        </p>
      </div>
      <div v-show="!showPreview" class="upload-box">
        <el-upload
          ref="upload"
          class="upload-wrap"
          drag
          accept=".xls,.xlsx"
          :action="action"
          :data="extraParam"
          :show-file-list="true"
          :multiple="false"
          :auto-upload="false"
          :file-list="fileList"
          :on-change="handlerChange"
          :on-success="handlerSuccess"
          :on-error="handlerError"
          :before-upload="beforeUpload"
          :limit="1"
        >
          <div class="upload_file_choose">
            <i class="el-icon-upload cor-blue"></i>
            <div class="upload_file_choose_text">
              {{ $t('batchUpload.drag1') }}<em>{{ $t('batchUpload.drag2') }}</em
              ><br /><span class="support-tip">{{ supportFormat }}</span>
            </div>
          </div>
        </el-upload>
      </div>
      <div class="download-template">
        <el-button type="text" icon="icon-xiazai1" @click="downloadTpl"
          >{{ $t('batchUpload.downloadTpl') }}
        </el-button>
      </div>
    </div>
    <span slot="footer">
      <el-button type="primary" :disabled="disableSubmit" @click="submit">{{
        $t('batchUpload.submit')
      }}</el-button>
      <el-button @click="v = false">{{ $t('common.cancel') }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  components: {},
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    queryId: {
      type: String,
      default: '2000',
    },
    maxFileSize: {
      type: Number,
      default: 10 * 1024 * 1024,
    },
    title: {
      type: String,
      default: '批量操作',
    },
    isXls: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      uploaded: false,
      loading: false,
      fileList: [],
      fileName: '',
      extraParam: {},
    }
  },
  computed: {
    action() {
      return process.env.VUE_APP_REQUEST_BASE_API + '/ruleCommon/upload'
    },
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
    supportFormat() {
      return this.$t('batchUpload.drag3') + ' ' + (this.isXls ? '.xls' : '.xlsx')
    },
    disableSubmit() {
      return this.loading
    },
    downloadUrl() {
      return '/order/download/' + this.queryId
    },
    showPreview() {
      return !this.uploaded && this.fileName
    },
    fileSizeFormat() {
      if (this.fileSize > 1024 * 1024) {
        return (this.fileSize / 1024 / 1024).toFixed(2) + 'MB'
      } else {
        return (this.fileSize / 1024).toFixed(2) + 'KB'
      }
    },
  },
  methods: {
    downloadTpl() {
      window.open(this.downloadUrl, '_self')
    },
    handlerChange(file, fileList) {
      this.fileList.push(file.raw)
      if (fileList.length > 1) {
        fileList.splice(0, 1)
      }
    },
    handlerSuccess(response, file, fileList) {
      if (response.data && response.data.length > 0) {
        this.errAlert(response.data)
        return
      }
      // this.uploaded = true
      // const timer = setTimeout(() => {
      //   this.loading = false
      //   clearTimeout(timer)
      // }, 200)
      // const self = this
      // const h = this.$createElement
      // this.$msgbox({
      //   title: this.$t('batchUpload.subtmitSuccess'),
      //   type: 'success',
      //   center: true,
      //   message: h('p', { class: 'fs12' }, [
      //     this.$t('batchUpload.task'),
      //     h(
      //       'a',
      //       { class: 'cor-blue', on: { click: self.viewProgress } },
      //       this.$t('batchUpload.process')
      //     ),
      //   ]),
      //   showCancelButton: false,
      //   confirmButtonText: this.$t('batchUpload.know'),
      // })
      //   .then(() => {
      //     this.uploaded = false
      //     this.v = false
      //   })
      //   .catch(() => {})
    },
    resetUpload() {
      this.$refs.upload.clearFiles()
      this.uploaded = false
      this.fileName = ''
      this.fileSize = 0
    },
    handlerError(err) {
      const timer = setTimeout(() => {
        this.loading = false
        clearTimeout(timer)
      }, 200)
      // 第一种错误提示类型
      let errMsg = this.$t('batchUpload.systemError')
      if (err.message) {
        const parse = JSON.parse(err.message)
        if (parse && parse.message) {
          errMsg += '，' + parse.message
        }
      }
      this.$msgbox({
        title: this.$t('batchUpload.subtmitFail'),
        type: 'warning',
        center: true,
        message: errMsg,
        showCancelButton: false,
        confirmButtonText: this.$t('batchUpload.know'),
      })
        .then(() => {})
        .catch(() => {})
      this.$nextTick(this.resetUpload)
    },
    errAlert(errMsgList) {
      this.$nextTick(this.resetUpload)
      const timer = setTimeout(() => {
        this.loading = false
        clearTimeout(timer)
      }, 200)
      // 第二种错误提示类型
      const h = this.$createElement
      const msg = h('div', { class: 'error-msg-box' }, [
        ...(function () {
          const arr = []
          errMsgList.forEach((item) => {
            arr.push(h('p', { class: 'cor-red' }, item))
          })
          return arr
        })(),
      ])
      this.$msgbox({
        title: this.$t('batchUpload.subtmitFail'),
        type: 'warning',
        center: true,
        message: msg,
        showCancelButton: false,
        confirmButtonText: this.$t('batchUpload.know'),
      })
        .then(() => {})
        .catch(() => {})
    },
    toReload() {
      this.resetUpload()
    },
    submit() {
      this.v = false
    },
    gitUpload() {
      this.$refs.upload.clearFiles()
      this.$nextTick(() => {
        this.$refs.upload.handleStart(this.fileList[0])
        this.$refs.upload.submit()
      })
    },
    beforeUpload(file) {
      if (this.fileSize > this.maxFileSize) {
        let maxFileSizeView = ''
        if (this.maxFileSize > 1024 * 1024) {
          maxFileSizeView = this.maxFileSize / 1024 / 1024 + 'MB'
        } else {
          maxFileSizeView = this.maxFileSize / 1024 + 'KB'
        }
        this.$message.error(this.$t('batchUpload.fileSize') + maxFileSizeView)
        return false
      }
      if (
        file.type !== 'application/vnd.ms-excel' &&
        file.type !== 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      ) {
        this.$message.error(this.$t('batchUpload.fileType'))
        return false
      }
      return true
    },
    viewProgress() {
      this.uploaded = false
      this.v = false
      this.$msgbox.close()
      this.$router.push('/common/download')
    },
  },
}
</script>

<style lang="scss" scoped>
