  background: #f3f5f7;
  font-size: 14px;
  color: #666;
  line-height: 30px;
  border-radius: 4px;
  padding: 20px;
  margin-top: 15px;
  margin-bottom: 20px;
  position: relative;

  .icon-zhuyi {
    position: absolute;
  }
  .upload-tip {
    margin-left: 20px;
    &::first-line {
      color: #333;
    }
  }
}
.support-tip {
  font-weight: 400;
  font-size: 14px;
  color: #999999;
}
::v-deep .el-upload {
  width: 100%;
  .el-upload-dragger {
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.upload_file_choose {
  i {
    color: #2b6ffb;
  }

  &_text {
    line-height: 25px;
  }
}
.download-template {
  display: flex;
  ::v-deep .el-button {
    margin-left: auto;
  }
  margin: 12px 0;
}
::v-deep .el-dialog__footer {
  border-top: 1px solid #d6d6d6;
  padding-top: 20px;
  padding-bottom: 20px;
  // .el-button:first-child {
  //   width: 86px;
  // }
  // .el-button:last-child {
  //   width: 60px;
  // }
}
.error-msg-box {
  border: 1px solid #cad4e0;
  padding: 15px;
  max-height: 230px;
  overflow: auto;
  margin-top: 10px;
  p {
    text-align: left;
    line-height: 1.2;
    font-size: 12px;
  }
}
</style>
