<template>
  <div class="two-box">
    <div class="box-title">
      <span>{{ $t('rule.defineRule') }}</span
      >[{{ supplier.attrValueName }}]
    </div>
    <div class="box-bottom">
      <div class="box-small-title">{{ $t('rule.selected') }}</div>
      <div class="box-mini">
        <div v-for="(item, index) in miniList" :key="index" class="box-mini-box">
          <div class="box-mini-title">{{ item.name }}</div>
          <div class="box-mini-value">
            {{ item.value }}
          </div>
        </div>
      </div>
      <div class="box-form">
        <div class="box-small-title">{{ $t('rule.ruleConfiguration') }}</div>
        <el-form
          ref="ruleForm"
          :model="ruleForm"
          size="small"
          label-position="top"
          :rules="rules"
        >
          <!-- 规则名称 -->
          <el-form-item :label="$t('rule.ruleName')" prop="ruleName">
            <el-input
              v-model="ruleForm.ruleName"
              class="w400"
              :placeholder="inputPh('rule.ruleName')"
            ></el-input>
          </el-form-item>
          <!-- 对象类型 -->
          <el-form-item
            v-if="hasTargetType"
            :label="$t('rule.objectType')"
            prop="objType"
          >
            <ct-select
              v-model="ruleForm.objType"
              class="w400"
              :placeholder="selectPh('rule.objectType')"
              :options="objOptions"
              :disabled="objDis"
            />
          </el-form-item>
          <!-- 套餐 -->
          <el-form-item
            v-if="ruleForm.objType == 2"
            :label="$t('rule.packageInfo')"
            prop="offerName"
          >
            <el-input
              v-model="ruleForm.offerName"
              class="w400"
              readonly
              :placeholder="selectPh('rule.packageInfo')"
            >
              <a
                slot="suffix"
                class="el-icon-s-order btn-blue"
                @click="selectPackageVisible = true"
              ></a>
            </el-input>
          </el-form-item>
          <!-- 阈值 -->
          <span v-if="thresholdSize">
            <el-form-item
              v-for="(item, index) in ruleForm.ruleValue"
              :key="index"
              :label="thresholdSizeLabel(index)"
            >
              <el-input
                v-model="ruleForm.ruleValue[index]"
                class="w400"
                :placeholder="thresholdPh(index)"
                @input="validateInput(index)"
              ></el-input
              >&nbsp;&nbsp;{{ unitName }} &nbsp;&nbsp;&nbsp;&nbsp;
              <el-button
                v-if="index > 0"
                icon="el-icon-minus"
                @click="delRuleValue(index)"
              ></el-button>
            </el-form-item>
          </span>
          <el-form-item>
            <el-button
              v-if="thresholdSize"
              icon="el-icon-plus"
              class="add-button"
              @click="addRuleValue"
              >{{ $t('rule.increase') }}</el-button
            >
          </el-form-item>
          <!-- 推送URL -->
          <el-form-item :label="$t('rule.pushURL')" prop="actionDest">
            <div>
              <el-input
                v-model="ruleForm.actionDest"
                class="w667"
                :placeholder="inputPh('rule.pushURL')"
              ></el-input>
              <el-button icon="el-icon-money" type="primary" @click="testAddress">{{
                $t('rule.testAddress')
              }}</el-button>
              <el-upload
                ref="uploadRef"
                class="upload-demo inline-block mar-r10"
                action="#"
                :http-request="doUploadFile"
                :headers="{
                  'Content-Type': 'multipart/form-data',
                }"
                :auto-upload="false"
                :multiple="false"
                :limit="1"
                :show-file-list="false"
                :accept="fileType.join()"
                :before-upload="beforeUpload"
                :on-exceed="handleExceed"
                :on-change="handleChange"
                :on-remove="handleRemove"
                :on-success="handleSuccess"
                :on-error="handleError"
                :file-list="fileList"
              >
                <el-button slot="trigger" type="primary" icon="el-icon-upload2">
                  {{ $t('rule.uploadCertificate') }}</el-button
                >
                <div v-if="fileName" class="upload-file" @click="handlePreview">
                  <i class="el-icon-document"></i>
                  {{ fileName }} <span @click="delFileList">x</span>
                </div>
              </el-upload>
            </div>
            <p class="url-pla">
              {{ $t('rule.httpPrompt') }}
            </p>
          </el-form-item>
        </el-form>
        <div v-if="false" class="box-two">
          <div class="two-title">{{ $t('rule.ruleObject') }}</div>
          <div class="table-btn">
            <el-button
              icon="el-icon-edit-outline"
              size="small"
              class="btn-blue"
              @click="handleUpload"
              >{{ $t('rule.batchImport') }}</el-button
            >
            <el-button
              icon="el-icon-delete"
              size="small"
              class="btn-red"
              @click="showUpload1 = true"
              >{{ $t('rule.batchDelete') }}</el-button
            >
          </div>
          <el-table
            ref="table"
            v-loading="loading"
            :element-loading-text="$t('rule.loading')"
            :data="tableData"
            :border="true"
            overflow-x="scroll"
            height="280px"
          >
            <el-table-column
              prop="iccid"
              label="ICCID"
              show-overflow-tooltip
              min-width="110"
            ></el-table-column>
            <el-table-column
              prop="msisdn"
              :label="$t('rule.accNumber')"
              show-overflow-tooltip
              min-width="110"
            ></el-table-column>
          </el-table>
          <el-pagination
            v-show="tableData.length > 0"
            background
            :current-page="page.pageIndex"
            align="right"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
        <BatchUpload
          v-show="showUpload"
          ref="addUpload"
          :visible.sync="showUpload"
          query-id="2000"
          :max-file-size="10 * 1024 * 1024"
          :title="$t('rule.batchImportNumber')"
        >
          <template #tip>
            1、{{ $t('batchUpload.uploadTip1') }}<br />
            2、{{ $t('batchUpload.uploadTip2') }}<br />
            3、{{ $t('batchUpload.uploadTip3') }}<br />
            4、{{ $t('batchUpload.uploadTip4') }}
          </template>
        </BatchUpload>
        <BatchUpload
          v-show="showUpload1"
          ref="delUpload"
          :visible.sync="showUpload1"
          query-id="2001"
          :max-file-size="10 * 1024 * 1024"
          :title="$t('rule.batchDeleteNumber')"
        >
          <template #tip>
            1、{{ $t('batchUpload.uploadTip1') }}<br />
            2、{{ $t('batchUpload.uploadTip2') }}<br />
            3、{{ $t('batchUpload.uploadTip3') }}<br />
            4、{{ $t('batchUpload.uploadTip4') }}
          </template>
        </BatchUpload>
        <Package
          v-if="selectPackageVisible"
          :visible.sync="selectPackageVisible"
          :supplier-nbr="supplierNbr"
          @get="getPackage"
        ></Package>
      </div>
    </div>
  </div>
</template>
<script>
import { DCP_ADDRESS_ID, CT_ADDRESS_ID } from '@/utils/constant'
import langMixin from '@/mixins/langMixin'
import BatchUpload from './BatchUpload'
import Package from './package.vue'
import {
  i18nKeyFromSubType,
  unitKeyFromSubType,
  hideObjType,
  ruleTypeFromSubType,
  i18nKeyFromRuleType,
} from '../../constant'
export default {
  components: { BatchUpload, Package },
  mixins: [langMixin],
  props: {
    supplierNbr: {
      type: String,
      default: '0',
    },
    supplier: {
      type: Object,
      default: () => ({}),
    },
    ruleSubType: {
      type: Number,
      default: 0,
    },
    // 二进制数据，类型为对象，默认值为一个空对象
    twoBoxData: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      selectPackageVisible: false,
      objDis: false,
      uploadParam: {},
      loading: false,
      showUpload1: false,
      showUpload: false,
      fileType: ['.pem', '.crt', '.cer'],
      fileName: '',
      fileList: [],
      tableData: [],
      ruleForm: {
        actionDest: '',
        ruleName: '',
        objType: '',
        ruleValue: [''],
        offerName: '',
      },
      i18nKeyFromSubType,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableVisible: true,
    }
  },
  computed: {
    showObjTypeTable() {
      return this.ruleForm.objType == 3 && Object.keys(this.$route.params).length > 0
    },
    objOptions() {
      let options = [
        { value: '1', label: this.$t('rule.enterprise') },
        { value: '2', label: this.$t('rule.package') },
        { value: '3', label: this.$t('rule.simCard') },
      ]
      options = options.filter((option) =>
        this.supplier.targetTypeList.find((item) => item.attrValue === option.value)
      )
      return options
    },
    miniList() {
      return [
        {
          name: this.$t('rule.category'),
          value: this.$t(i18nKeyFromRuleType(ruleTypeFromSubType(this.ruleSubType))),
        },
        {
          name: this.$t('rule.type'),
          value: this.$t(i18nKeyFromSubType(this.ruleSubType)),
        },
      ]
    },
    rules() {
      return {
        actionDest: [
          { required: true, message: this.inputPh('rule.pushURL'), trigger: 'blur' },
        ],
        ruleName: [
          {
            required: true,
            message: this.inputPh('rule.ruleName'),
            trigger: 'blur',
          },
        ],
        objType: [
          { required: true, message: this.selectPh('rule.objectType'), trigger: 'blur' },
        ],
        offerName: [
          { required: true, message: this.selectPh('rule.packageInfo'), trigger: 'blur' },
        ],
      }
    },
    hasTargetType() {
      if (!this.supplier?.targetTypeList) {
        return false
      }
      if (hideObjType(this.ruleSubType)) {
        return false
      }
      return this.supplier.targetTypeList.length > 0
    },
    thresholdSize() {
      if (hideObjType(this.ruleSubType)) {
        return false
      }
      if (this.supplier && this.supplier.thresholdSizeList?.length > 0) {
        return Number(this.supplier.thresholdSizeList[0].attrValue)
      }
      return 0
    },
    unitName() {
      return this.$t(unitKeyFromSubType(this.ruleSubType))
    },
  },
  watch: {
    twoBoxData(val) {
      if (val) {
        this.fileName = val.fileName
        this.queryRuleSimRecordList()
      }
    },
  },
  mounted() {
    if (Object.keys(this.$route.params).length > 0) {
      const row = JSON.parse(this.$route.params.row)
      const objType = row.objType + ''
      const ruleValue = row.ruleValue.split(',')
      this.ruleForm = { ...row, objType, ruleValue, offerName: '', offerId: '' }
    } else {
      // 设置默认对象类型
      if (hideObjType(this.ruleSubType)) {
        this.ruleForm.objType = 1
      }
    }
  },
  methods: {
    thresholdSizeLabel(index) {
      if (this.ruleForm.ruleValue.length > 1) {
        return this.$t('rule.threshold') + (index + 1)
      }
      return this.$t('rule.threshold')
    },
    validateInput(index) {
      const value = this.ruleForm.ruleValue[index]
      const regex = /^[1-9]\d*$/ // 正则表达式匹配正整数
      if (!regex.test(value)) {
        this.$set(this.ruleForm.ruleValue, index, '') // 清除无效输入
      }
    },
    thresholdPh(index) {
      if (this.ruleForm.ruleValue.length === 1) {
        return this.$t('rule.triggerCountPh1') + `(${this.$t(this.unitName)})`
      }
      return (
        this.$t('rule.triggerCountPh', { index: index + 1 }) +
        `(${this.$t(this.unitName)})`
      )
    },
    getPackage(item) {
      this.ruleForm.offerName = item.offerName
      this.ruleForm.offerId = item.offerId
      this.$refs.ruleForm.validateField('offerName')
    },
    delFileList() {
      this.fileName = ''
      this.$refs.uploadRef.clearFiles()
      this.fileList = []
    },
    // 点击文件列表中已上传文件进行下载
    handlePreview() {
      if (this.twoBoxData.ruleId) {
        window.open(
          `${process.env.VUE_APP_REQUEST_BASE_API}/ruleCommon/download?ruleId=${this.twoBoxData.ruleId}`,
          '_self'
        )
      }
    },
    handleUpload() {
      this.showUpload = true
    },
    addRuleValue() {
      if (this.ruleForm.ruleValue.length === this.thresholdSize) {
        return
      }
      this.ruleForm.ruleValue.push('')
    },
    delRuleValue(index) {
      if (this.ruleForm.ruleValue.length === 1) return
      this.ruleForm.ruleValue.splice(index, 1)
    },
    // 上传证书
    doUploadFile(params) {
      // this.uploadParam = {
      //   file: params.file,
      // }
    },
    getSave(item) {
      this.uploadParam.file = this.fileList[0].raw
      this.uploadParam.ruleId = item
      return this.$api.rule.uploadHttpsCert(this.uploadParam)
    },
    beforeUpload(file) {
      return true
    },
    handleExceed(files, fileList) {
      this.$set(fileList[0], 'raw', files[0])
      this.$set(fileList[0], 'name', files[0].name)
      this.fileName = files[0].name
      this.fileList = fileList.slice(-1)
    },
    handleChange(file, fileList) {
      this.fileList = fileList.slice(-1)
      this.fileName = file.name
    },
    handleRemove(file, fileList) {
      this.fileList = fileList.slice(-1)
    },
    handleSuccess(response, file, fileList) {
      this.$refs.uploadRef.clearFiles()
      this.fileList = []
    },
    handleError(err, file, fileList) {
      console.log(err)
      this.$refs.uploadRef.clearFiles()
      this.fileList = []
    },
    async testAddress() {
      const encodedUrl = encodeURIComponent(this.ruleForm.actionDest)
      await this.$api.rule.urlConnectTest({ url: encodedUrl })
      this.$message.success(this.$t('rule.testSuccess'))
    },
    queryRuleSimRecordList() {
      if (this.supplierNbr === DCP_ADDRESS_ID || this.supplierNbr === CT_ADDRESS_ID) {
        return (this.tableVisible = false)
      }
      const ruleId = this.twoBoxData.ruleId
      this.$api.rule.queryRuleSimRecordList({ ruleId }).then((res) => {
        if (res.list && res.list.length > 0) {
          this.tableData = res.list
          this.page.total = res.total
        } else {
          this.tableData = []
          this.page.total = 0
        }
      })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.queryRuleSimRecordList()
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.queryRuleSimRecordList()
    },
  },
}
</script>
