<template>
  <el-dialog
    :title="$t('rule.packageInfo')"
    :visible.sync="v"
    :append-to-body="true"
    width="940px"
    top="15vh"
  >
    <div>
      <el-form ref="form" size="small" :model="searchForm">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item :label="$t('rule.packageName')">
              <el-input
                v-model="searchForm.offerName"
                :placeholder="$t('rule.enter') + $t('rule.packageName')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="18" style="text-align: right">
            <el-form-item>
              <label slot="label">&nbsp;</label>
              <el-button
                class="w70 mar-l10"
                type="primary"
                icon="el-icon-search"
                @click="qryAccountPage(false)"
                >{{ $t('rule.query') }}</el-button
              >
              <el-button
                class="w70"
                type="info"
                icon="el-icon-refresh"
                @click="reset()"
                >{{ $t('rule.resetting') }}</el-button
              >
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-table
        ref="roleTable"
        v-loading="loading"
        :element-loading-text="$t('rule.loading')"
        :class="{ 'empty-table': accountList.length === 0 }"
        :data="accountList"
        border
        size="small"
        stripe
        :height="300"
        highlight-current-row
        @current-change="handleSelectionChange"
      >
        <el-table-column width="40px">
          <template slot-scope="scope">
            <el-radio v-model="radio" :label="scope.row.offerId">
              <span class="el-radio__label"></span>
            </el-radio>
          </template>
        </el-table-column>
        <el-table-column
          prop="offerName"
          :label="$t('rule.packageName')"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="supplierNbrDesc"
          :label="$t('rule.supplier')"
          show-overflow-tooltip
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="accountList.length > 0"
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <span slot="footer">
      <el-button size="small" type="primary" @click="clickOK">{{
        $t('rule.determine')
      }}</el-button>
      <el-button size="small" @click="v = false">{{ $t('rule.cancellation') }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  components: {},
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    supplierNbr: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      loading: false,
      searchForm: {
        offerName: '',
      },
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      accountList: [],
      radio: '',
      selectedRow: null,
    }
  },
  computed: {
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  mounted() {
    this.qryAccountPage()
  },
  methods: {
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryAccountPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryAccountPage(true)
    },
    // 传递参数回父组件
    clickOK() {
      if (this.selectedRow != null) {
        console.log(this.selectedRow)
        this.$emit('get', this.selectedRow)
      }
      this.v = false
    },
    handleSelectionChange(row) {
      if (row) {
        this.selectedRow = row
        this.radio = row.offerId
      }
    },
    qryAccountPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.searchForm,
        ...this.page,
        supplierNbr: this.supplierNbr,
      }
      this.loading = true
      this.$api.rule
        .qryOfferList(param)
        .then((res) => {
          this.accountList = res.list || []
          this.page.total = res.total
        })
        .finally(() => {
          this.loading = false
        })
    },
    reset() {
      this.searchForm = {
        offerName: '',
      }
      this.radio = ''
      this.qryAccountPage()
    },
  },
}
</script>

<style lang="scss" scoped></style>
